import { IResult } from '@/types';
import { AxiosResponse } from 'axios';
import React, { FC, ReactElement, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { axiosInst } from '../../services/api';
import { SearchOverlay } from '../Search/overlay';
import style from './details.module.scss';

type OverlayState = 'empty' | 'noResults' | 'loading' | '';

export const DetailsPage: FC = (): ReactElement => {
  const [overlay, setOverlay] = useState<OverlayState>('loading');
  const [model, setModel] = useState<IResult | undefined>(undefined);
  const [error, setError] = useState<boolean>(false);

  const { uid } = useParams<{ uid: string }>();
  const query = `/v3/models/${uid}`;

  const sendRequest = async () => {
    try {
      const response: AxiosResponse<IResult> = await axiosInst.get(query);
      setModel(response.data);
      if (response.data) {
        setOverlay('');
      }
    } catch (err) {
      setOverlay('noResults');
      setError(true);
      console.error(err);
    }
  };

  useEffect(() => {
    sendRequest();
  }, [uid]);

  return (
    <div className={style.container}>
      <div className={style.content} area-hidden={`${error}`}>
        <div className={style.iframeWrapper}>
          <iframe src={model?.embedUrl}></iframe>
        </div>
        <div className={style.nameStats}>
          <h2 className={style.modelName}>{model?.name}</h2>
          <div className={style.statistic}>
            <span className={`${style.stats} ${style.views}`} title="Views">
              {model?.viewCount}
            </span>
            <span className={`${style.stats} ${style.likes}`} title="Like">
              {model?.likeCount}
            </span>
          </div>
        </div>
        <p className={style.description}>{model?.description}</p>
        <p className={style.prop}>
          <span className={style.propName}>Published:</span>
          {model?.publishedAt.slice(0, 10)}
        </p>
        <div className={style.user}>
          <div className={style.avatar}>
            <img src={model?.user.avatar.images[0].url} alt="avatar" />
          </div>{' '}
          <div className={style.userName}>{model?.user.displayName}</div>
        </div>
        <div className={style.metaStats}>
          <p className={style.prop}>
            <span className={style.propName}>Triangles:</span>
            {model?.faceCount}
          </p>
          <p className={style.prop}>
            <span className={style.propName}>Vertices:</span>
            {model?.vertexCount}
          </p>
        </div>
        <div className={style.tags}>
          <span className={style.propName}>Tags:</span>
          {model?.tags.map(item => (
            <span className={style.tag} key={item.slug}>
              {item.name}
            </span>
          ))}
        </div>
      </div>
      <SearchOverlay state={overlay} />
    </div>
  );
};
