import React, { ReactElement } from 'react';
import ReactDom from 'react-dom';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import styled, { keyframes } from 'styled-components';
import { fadeInRight } from 'react-animations';
import './app.scss';
import { AboutPage } from './components/About/about';
import { DetailsPage } from './components/Details/details';
import { ErrorPage } from './components/Error-page/error';
import { Header } from './components/Header/header';
import { SearchPage } from './components/Search/search';

const navData = [
  {
    component: <SearchPage />,
    path: '/',
    isExact: true,
  },
  {
    component: <AboutPage />,
    path: '/about',
    isExact: true,
  },
  {
    component: <DetailsPage />,
    path: '/details/:uid',
    isExact: true,
  },
  {
    component: <ErrorPage />,
    path: '/*',
    isExact: false,
  },
];

const SlideBox = styled.div`
  animation: 0.5s ${keyframes`${fadeInRight}`};
`;

export const App = (): ReactElement => {
  return (
    <React.StrictMode>
      <BrowserRouter>
        <Header />
        <Switch>
          {navData.map(({ component, path, isExact }) => {
            return (
              <Route
                path={path}
                exact={isExact}
                key={path}
                render={() => <SlideBox>{component}</SlideBox>}
              />
            );
          })}
        </Switch>
      </BrowserRouter>
    </React.StrictMode>
  );
};

ReactDom.render(<App />, document.getElementById('root') as HTMLElement);
