import { GET200Response, IResult } from '@/types';
import { AxiosResponse } from 'axios';
import React, { ChangeEvent, useState } from 'react';
import { axiosInst } from '../../services/api';
import { Searchbar } from './searchbar';
import { Card } from '../Card/card';
import { SearchOverlay } from './overlay';
import style from './search.module.scss';
import { SearchSettings } from './settings';

export const SearchPage = (): JSX.Element => {
  const [searchVal, setSearchVal] = useState<string>('');
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [PrevPage, setPrevPage] = useState<string>('');
  const [NextPage, setNextPage] = useState<string>('');
  const [models, setModels] = useState<IResult[]>([]);
  const [sort, setSort] = useState<string>('');
  const [itemsPerPage, setItemsPerPage] = useState<string>('24');
  const [contentPage, setPage] = useState('1');
  const [overlay, setOverlay] = useState<OverlayState>('empty');

  const loadingResults = (isLoad: boolean) => {
    setIsLoading(isLoad);
    if (isLoad) {
      setOverlay('loading');
    }
  };

  const sendRequest = async (
    searchV?: string,
    sortV?: string,
    count?: string,
    page?: string,
    query?: string,
  ) => {
    loadingResults(true);
    const queryParams = [
      `q=${searchV || searchVal}`,
      `sort_by=${sortV || sort}`,
      `count=${count || itemsPerPage}`,
      `cursor=${(Number(page || contentPage) - 1) * Number(itemsPerPage)}`,
    ];
    const q = query || `v3/search?type=models&${queryParams.join('&')}`;
    try {
      const response: AxiosResponse<GET200Response> = await axiosInst.get(q);
      setModels(response.data.results);
      if (response.data.results.length === 0) {
        setOverlay('noResults');
      } else {
        setOverlay('');
      }
      if (response.data.previous) {
        setPrevPage(response.data.previous.slice(25));
      } else {
        setPrevPage('');
      }
      if (response.data.next) {
        setNextPage(response.data.next.slice(25));
      } else {
        setNextPage('');
      }
    } catch (err) {
      console.error(err);
    } finally {
      loadingResults(false);
    }
  };

  const handleSubmit = async (e: ChangeEvent<HTMLFormElement>) => {
    e.preventDefault();
    sendRequest();
  };

  const handleSort = (e: ChangeEvent<HTMLSelectElement>) => {
    const { value } = e.target;
    setSort(value);
    sendRequest(undefined, value);
  };

  const handleItemsPerPage = (e: ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;
    setItemsPerPage(value);
    sendRequest(undefined, undefined, value);
  };

  const handleContentPage = (e: ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;
    setPage(value);
    sendRequest(undefined, undefined, undefined, value);
  };

  const clickPageBtn = (p: -1 | 1) => {
    const newQuery = p === -1 ? PrevPage : NextPage;
    setPage(`${Number(contentPage) + p}`);
    sendRequest(undefined, undefined, undefined, undefined, newQuery);
  };

  return (
    <main className={style.main}>
      <Searchbar
        searchState={{ value: searchVal, dispatch: setSearchVal }}
        isLoading={isLoading}
        handleSubmit={handleSubmit}
      />
      <SearchSettings
        sortState={{ value: sort, handle: handleSort }}
        itemsPPState={{ value: itemsPerPage, handle: handleItemsPerPage }}
        curPageState={{ value: contentPage, handle: handleContentPage }}
      />
      <output className={style.cards}>
        {models.map(item => (
          <Card model={item} key={item.uid} />
        ))}
      </output>
      <div className={style.pagination}>
        <button className={style.btn} disabled={!PrevPage} onClick={() => clickPageBtn(-1)}>
          prev
        </button>
        <button className={style.btn} disabled={!NextPage} onClick={() => clickPageBtn(1)}>
          next
        </button>
      </div>
      <SearchOverlay state={overlay} />
    </main>
  );
};

type OverlayState = 'empty' | 'noResults' | 'loading' | '';
