import { IResult } from '@/types';
import React, { FC } from 'react';
import { Link } from 'react-router-dom';
import style from './card.module.scss';

const THOUSAND = 1000;

export const Card: FC<{ model: IResult }> = ({ model }): JSX.Element => {
  return (
    <div className={style.cardWrap}>
      <Link to={`/details/${model.uid}`} className={style.card}>
        <div className={style.main}>
          <span className={style.modelLink}>
            <img src={model.thumbnails.images[0].url} alt="Model image" />
          </span>
        </div>
        <div className={style.footer}>
          <div className={style.left}>
            <span className={style.avatar}>
              <img src={model.user.avatar.images[0].url} alt={model.user.displayName} />
            </span>
            <span className={style.modelLink}>
              <p className={style.title}>{model.name}</p>
            </span>
          </div>
          <div className={style.right}>
            <span className={`${style.stats} ${style.views}`} title="Views">
              {model.viewCount >= THOUSAND
                ? `${(model.viewCount / THOUSAND).toFixed(1)}k`
                : model.viewCount}
            </span>
            <span className={`${style.stats} ${style.comments}`} title="Comment">
              {model.commentCount >= THOUSAND
                ? `${(model.commentCount / THOUSAND).toFixed(1)}k`
                : model.commentCount}
            </span>
            <span className={`${style.stats} ${style.likes}`} title="Like">
              {model.likeCount >= THOUSAND
                ? `${(model.likeCount / THOUSAND).toFixed(1)}k`
                : model.likeCount}
            </span>
          </div>
        </div>
      </Link>
    </div>
  );
};
