import React, { ChangeEvent, FC, ReactElement } from 'react';
import style from './search.module.scss';

const sorting = [
  'relevance',
  'likeCount',
  '-likeCount',
  'viewCount',
  '-viewCount',
  'publishedAt',
  '-publishedAt',
  'processedAt',
  '-processedAt',
];

type SettingProps = {
  sortState: {
    value: string;
    handle: (e: ChangeEvent<HTMLSelectElement>) => void;
  };
  itemsPPState: {
    value: string;
    handle: (e: ChangeEvent<HTMLInputElement>) => void;
  };
  curPageState: {
    value: string;
    handle: (e: ChangeEvent<HTMLInputElement>) => void;
  };
};

export const SearchSettings: FC<SettingProps> = ({
  sortState,
  itemsPPState,
  curPageState,
}): ReactElement => {
  return (
    <div className={style.header}>
      <label htmlFor="sort" className={style.setting}>
        <span className={style.fieldName}>Sort by:</span>
        <select name="sort" id="sort" value={sortState.value} onChange={sortState.handle}>
          {sorting.map((item, index) => (
            <option value={item} key={index}>
              {item}
            </option>
          ))}
        </select>
      </label>
      <label htmlFor="modelsPerPage" className={style.setting}>
        <span className={style.fieldName}>Models on page:</span>
        <input
          type="number"
          name="modelsPerPage"
          id="modelsPerPage"
          value={itemsPPState.value}
          onChange={itemsPPState.handle}
        />
      </label>
      <label htmlFor="page" className={style.setting}>
        <span className={style.fieldName}>Page:</span>
        <input
          type="number"
          name="page"
          id="page"
          value={curPageState.value}
          onChange={curPageState.handle}
          min="1"
        />
        {/* <span className={style.fieldName}>
          of {Math.ceil(Number(models.length) / Number(itemsPerPage))}
        </span> */}
      </label>
    </div>
  );
};
