import React, { FC } from 'react';
import style from './search.module.scss';

const states = {
  empty: 'Type something to search',
  noResults: 'No results',
  loading: 'Loading...',
};

export const SearchOverlay: FC<EmptyState> = (props): JSX.Element => {
  return (
    <div className={style.overlay} area-hidden={`${props.state === ''}`}>
      <div className={style.empty}>
        <div
          className={`${style.state} ${style.trySearch}`}
          area-hidden={`${props.state !== 'empty'}`}
        ></div>
        <div
          className={`${style.state} ${style.loading}`}
          area-hidden={`${props.state !== 'loading'}`}
        ></div>
        <div
          className={`${style.state} ${style.noResults}`}
          area-hidden={`${props.state !== 'noResults'}`}
        ></div>
        <p className={style.text}>{props.state ? states[props.state] : ''}</p>
      </div>
    </div>
  );
};

type EmptyState = {
  state: 'empty' | 'noResults' | 'loading' | '';
};
