import React, { ChangeEvent, FC, ReactElement } from 'react';
import { Logo } from '../logo';
import style from './search.module.scss';

type SearchBarProps = {
  searchState: {
    value: string;
    dispatch: React.Dispatch<React.SetStateAction<string>>;
  };
  isLoading: boolean;
  handleSubmit: (e: ChangeEvent<HTMLFormElement>) => Promise<void>;
};

export const Searchbar: FC<SearchBarProps> = ({
  searchState,
  isLoading,
  handleSubmit,
}): ReactElement => {
  const handleChangeVal = (e: ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;
    searchState.dispatch(value);
  };

  return (
    <header className={style.header}>
      <Logo />
      <form className={style.searchForm} onSubmit={handleSubmit}>
        <label htmlFor="search" className={style.search}>
          <span className={style.searchIcon}></span>
          <input
            type="text"
            name="search"
            id="search"
            value={searchState.value}
            onChange={handleChangeVal}
            disabled={isLoading}
            placeholder="Search 3D models"
          />
        </label>
        <button type="submit" className={style.searchBtn} disabled={isLoading}>
          {isLoading ? 'Loading...' : 'Search'}
        </button>
      </form>
    </header>
  );
};
